.page {
  font-family: "PT Sans", "Calibri", "Arial", sans-serif;
  font-weight: 400;
  font-style: normal;
  font-stretch: normal;
  font-variant: normal;
  font-size: @font-size-regular;
  line-height: @line-height-regular;
  background-color: @bg-color-white;
  min-width: 320px;
  min-height: 100%;
}

.page__body {
  display: flex;
  flex-direction: column;
  overflow: visible;
  position: relative;
  text-align: center;
  text-transform: uppercase;
  font-size: 14px;
  font-weight: normal;
  margin: 0 auto;
  padding: 0;
  height: 100%;
  max-width: 1200px;

  @media @desktop {
    box-shadow: 0 5px 15px rgba(0, 1, 1, 0.2);
  }
}

